export class Volta {

    constructor(corrida, numero, piloto, hora, tempo, velocidadeMedia) {
        this._corrida = corrida;
        this._numero = numero;
        this._piloto = piloto;
        this._hora = hora;
        this._tempo = tempo;
        this._velocidadeMedia = velocidadeMedia; // calculada pelo tempo e o comprimento da pista

        Object.freeze(this);
    }

    get corrida() {
        return this._corrida;
    }
    
    get numero() {
        return this._numero;
    }
    
    get piloto() {
        return this._piloto;
    }

    get hora() {
        return this._hora;
    }

    get tempo() {
        return this._tempo;
    }

    get velocidadeMedia() {
        return this._velocidadeMedia;
    }
}